import axios from 'axios';

const lambdaUrl = "https://dz483entn2.execute-api.us-east-2.amazonaws.com/entry";

export const getAllNotesByUser = async (userStr) => (await axios.post(lambdaUrl, {
  method: "GET",
  user: userStr,
})).data;

export const getNoteById = async (idStr) => (await axios.post(lambdaUrl, {
  method: "GET",
  byId: true,
  id: idStr,
})).data;

export const createNote = async (postObj) => (await axios.post(lambdaUrl, {
  method: "POST",
  post: postObj,
})).data;

export const updateNoteById = async (idStr,fieldsToUpdate) => (await axios.post(lambdaUrl, {
  method: "PUT",
  id: idStr,
  put: fieldsToUpdate,
})).data;

export const deleteNoteById = async (idStr) => (await axios.post(lambdaUrl, {
  method: "DELETE",
  id: idStr,
})).data;