import axios from 'axios';

const lambdaUrl = "https://a2dd6r2ld0.execute-api.us-east-2.amazonaws.com/user";

export const getUser = async (newUser) => (await axios.post(lambdaUrl, {
  method: "GET",
  get: newUser,
})).data;

export const createUser = async (postObj) => (await axios.post(lambdaUrl, {
  method: "POST",
  post: postObj,
})).data;

export const updateEmail = async (updateObj) => (await axios.post(lambdaUrl, {
  method: "PUT",
  put: updateObj,
})).data;

export const deleteUser = async (user) => (await axios.post(lambdaUrl, {
  method: "DELETE",
  delete: user,
})).data;