export default (theme) => ({
  root: {
    margin: '4px 0',
    padding: '4px',
    backgroundColor: '#9fc',
    borderRadius: '16px',
    textAlign: 'left',    
  },
  body: {
    padding: '8px 12px',
    backgroundColor: '#fff9',
    color: '#333',
    borderRadius: '16px',
  },
  timestamp: {
    color: '#666', 
    fontSize: '0.7em'
  },
  post: {
    margin: '8px',
    color: '#d7a',
  },
  footer: {
    display: 'flex',
    justifyContent: 'space-between',
    fontSize: '0.8em',        
  },
  prtyBadge: {    
    color: '#999',
    margin: 'auto 0',
  },
  button: {
    backgroundColor: '#fc66',
    textTransform: 'none',    
    '&:hover': {      
      backgroundColor: '#fc69',
      color: '#f9c',
    }
  },  
})