import React from 'react';
import './style.css';

export default function CreatePost({...props}) {
  return <div className="cp-root">
    <div className="cp-priority">
      <label><b>Priority</b></label>
      <select
        className="cp-select"
        name="priority"
        value={props.newNote.priority}
        onChange={(event)=>props.onChange(event)}
      >
        <option value={1}>1 (Highest)</option>
        <option value={2}>2</option>
        <option value={3}>3</option>
        <option value={4}>4</option>
        <option value={5}>5</option>
        <option value={6}>6</option>
        <option value={7}>7</option>
        <option value={8}>8</option>
        <option value={9}>9</option>
        <option value={10}>10 (Lowest)</option>
      </select>
    </div>
    <label className="cp-input-label"><b>Note</b></label>
    <textarea
      className="cp-input"    
      name="post"
      placeholder={`hit ENTER to submit`}
      value={props.newNote.post}
      rows={2}
      maxLength={140}
      onKeyPress={(event)=>{props.onKey(event)}}
      onChange={(event)=>{props.onChange(event)}}
    />    
    <div className="cp-charcount">{`${props.newNote.post.length}/140`}</div>
  </div>
}